@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;1,200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;1,200&display=swap);
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  background-color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
  width: 100%;
  height: 75px;
}

.header-logo {
  object-fit: contain;
  height: 35px;
  width: 150px;
  margin-left: 50px;
}

.header-center {
  display: flex;
  align-items: center;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  flex: 1 1;
  padding: 10px;
  height: 25px;
  border: 1px solid lightgray;
  border-radius: 999px;
  
}

.header-center > input {
  border: none;
  padding: 10px;
  outline-width: 0;
  width: 250px;
}

.header-right {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 15vw;
  margin-right: 50px;
  flex-wrap: wrap;
}


body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}

.banner {
  background: url("https://media.cntraveler.com/photos/5db1d0dd11c1e500092e7133/master/pass/airbnb-ski-aspen-28328347.jpg")
    center center;
  height: 65vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  align-content: center;
}

.banner-text {
  background-color: black;
  color: white;
  text-align: center;
  align-items: flex-end;
  width: 100%;
}

.banner-search {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.banner-search-button {
  background-color: white !important;
  font-weight: 900 !important;
  text-transform: inherit !important;
  color: #ff7779 !important;
}

.live-anywhere {
  margin-top: 70px;
}

.liveAnywhere-title {
  padding-left: 70px;
  font-family: "Poppins", sans-serif;
}
.card-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding: 0 70px;
}

.become-host {
  margin-top: 70px;
  position: relative;
  box-sizing: border-box;
  align-items: center;
}

.become-host > img {
  margin-top: 100px;
  border-radius: 20px;
  height: 490px;
  width: 1379.2px;
  display: block;
  align-items: center;
  align-content: center;
  margin: 0 70px 0 70px;
}

@media only screen and (max-width: 1444px) {
  .become-host > img {
    width: 90%;
  }
}

@media only screen and (max-width: 781px) {
  .become-host {
    width: 70%;
  }
  .content {
    flex-wrap: wrap;
  }
}

.content {
  display: flex;
  position: absolute;
  color: white;
  top: 140px;
  left: 150px;
  align-content: flex-start;
  flex-direction: column;
  /* border: 1px solid #000000; */
  width: 370px;
}

.content > h1 {
  font-size: 48px;
  line-height: 0px;
  font-family: Circular, -apple-system, BlinkMacSystemFont, Roboto,
    "Helvetica Neue", sans-serif;
}

.content > h3 {
  font-size: 18px;
  line-height: 24px;
  font-family: Circular, -apple-system, BlinkMacSystemFont, Roboto,
    "Helvetica Neue", sans-serif;
  font-size: 18px;
  letter-spacing: normal;
}

.button {
  margin-top: 20px !important;
  width: 120px !important;
  height: 45px !important;
  text-transform: inherit !important;
  background-color: white !important;
  border-radius: 9px !important;
  font-weight: bold !important;
}

.discover {
  margin-top: 70px;
}

.discover-title {
  padding-left: 70px;
  font-family: "Poppins", sans-serif;
  line-height: 10px;
}

.subTitle {
  padding-left: 70px;
  font-family: "Poppins", sans-serif;
}

.discover-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 70px 0 70px;
}

.cards {
  border-radius: 10px !important;
  height: 440px !important;
  width: 440px !important;
}

.card-imgs {
  border-radius: 10px !important;
}

.card-text {
  padding-left: 0 !important;
}

.discover-content {
  padding-right: 40px !important;
}

* {
  box-sizing: content-box;
}

.explore {
  margin-top: 70px;
}

.explore-title {
  padding-left: 70px;
  font-family: "Poppins", sans-serif;
  line-height: 10px;
}

.explore-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 0 70px 0 70px;
}

.footer {
  display: flex;
  align-items: center;
  align-content: center;
  border-top: 1px solid grey;
  margin: 150px 70px 0px 70px;
  justify-content: space-between;
}

.footer-right {
  display: flex;
  flex: 1 1;
  align-items: center;
  color: grey;
}

.footer-right > h3 {
  font-size: 13px;
  font-weight: 10px;
}

.footer-left {
  display: flex;
  align-items: center;
  padding-right: 50px;
  color: grey;
}

.footer-left > h3 {
  padding-right: 20px;
  padding-bottom: 10px;
}

.social-links {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.social-links > div {
  padding-right: 10px;
}

.lang-icon {
  padding-top: 50px;
}

